*{margin: 0;
padding: 0;
box-sizing: border-box;}

@font-face {
  font-family: 'S-CoreDream-3Light';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/S-CoreDream-3Light.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
li{
  list-style: none;
}

a{
  color: inherit;
  text-decoration: none;
}

body{
  background-color: wheat;
  font-family: 'S-CoreDream-3Light';
}

.list{
  max-width: 640px;
  width: calc(100% - 20px);
  margin: 20px auto;
  background-color: white;
}

h2{
  font-size: 30px;
  font-weight: 100;
  text-align: center;
}

form input{
  width: 100%;
  padding: 10px;
  font-size: 16px;
  font-weight: 200;
  border: none;
  border-bottom: 1px solid #333;
  margin-bottom: 10px;
}

button{
  width: 100%;
  height: 35px;
  font-size: 18px;
  background-color: rgb(204, 182, 140);
  border: none;
  color: #333;

}

li{
  border-bottom: 1px solid #333;
  height: 50px;
  line-height: 50px;
  padding: 0 40px;
}
li:nth-child(2n){
  background-color: wheat;
  position: relative;
}

li:nth-child(2n)::after{
  content: '🌞';
  width: 30px;
  height: 30px;
  position: absolute;
  top: 0;
  left: 10px;
}


li:nth-child(odd){
  position: relative;}
li:nth-child(odd)::after{
  position: absolute;
top: 0;
left: 10px;
width: 30px;
height: 30px;
content: '🎂';
}

li span::before{
content: '🤐';  
position: absolute;
  top: 0;
  right: 0;
  width: 30px;
  height: 30px;
cursor: pointer;
}




/* nth-child(2n) => 2배수 */
/* nth-child(odd) => 홀수 */
/* nth-child(ever) => 짝수 */
